import React, { useRef, useState } from 'react';
import { Swiper, SwiperSlide } from 'swiper/react';
import { EffectCards, Mousewheel } from 'swiper/modules';

import AppTheme from '../themes/AppTheme/AppTheme';
import CssBaseline from '@mui/material/CssBaseline';
import { Box, Tooltip, Typography, Stack, useMediaQuery, useTheme } from '@mui/material';
import Logo from '../assets/images/logo.svg'
import HelpOutlineIcon from '@mui/icons-material/HelpOutline';

import 'swiper/css';
import 'swiper/css/mousewheel';
import 'swiper/swiper-bundle.css';
import 'swiper/css/effect-cards';

import './GuestLayout.css'

function GuestLayout({
  children,
  slides={
    'Home': <Box>@pending-code</Box>,
    'Agents': <Box>@pending-code</Box>,
    'Pricing': <Box>@pending-code</Box>,
    'Contact': <Box>@pending-code</Box>,
  },
  ...rest
}){

  const swiperRef = useRef(null);
  const [activeIndex, setActiveIndex] = useState(0);
  const goToSlide = (i) => swiperRef.current?.slideTo(i);

  const theme = useTheme();
  const isSmall = useMediaQuery(theme.breakpoints.down('sm'));

  return (
    <AppTheme {...rest}>
      <CssBaseline enableColorScheme />
      
      <Box display="flex" flexDirection={isSmall ? 'column' : 'row'} height="100vh" sx={{ backgroundColor: '#EFEFEF', overflow: 'hidden' }}>
        {/* Left Side */}
        <Stack
          direction="column"
          spacing={2}
          p={2}
          py={4}
          bgcolor="grey.100"
          justifyContent="space-between"
          alignItems="center"
          height="100%"
          backgroundColor="#fff0"
        >

          {/* same logo but centered (using mui)*/}
          <Box display="flex" justifyContent="center" alignItems="center" width="100%">
            <img src={Logo} alt="Logo" width="70%" height="auto" />
          </Box>

          {/* Vertical Navigation Buttons */}
          <Stack direction="column" spacing={2} alignItems="center" width="100%">
            {Object.keys(slides).map((label, i) => {
              const isActive = activeIndex === i;
              return (
                <Box
                  key={i}
                  onClick={() => goToSlide(i)}
                  sx={{
                    display: 'flex',
                    flexDirection: 'row',
                    alignItems: 'center',
                    justifyContent: 'flex-start',
                    cursor: 'pointer',
                    color: isActive ? '#111' : '#333',
                    fontFamily: 'Inter, sans-serif',
                    fontWeight: 400,
                    fontSize: '1.4rem',
                    position: 'relative',
                    pr: 3,
                    width: '110px', // control alignment/centering
                  }}
                >
                  {/* Bullet */}
                  <Box
                    sx={{
                      width: 8,
                      height: 8,
                      borderRadius: '50%',
                      bgcolor: isActive ? '#111' : '#999',
                      mr: 1.5,
                      transition: 'background-color 0.3s ease',
                      flexShrink: 0,
                    }}
                  />
                  {/* Label with underline */}
                  <Box sx={{ position: 'relative', width: '100%' }}>
                    {label}
                    <Box
                      sx={{
                        position: 'absolute',
                        bottom: -2,
                        left: 0,
                        height: 2,
                        width: isActive ? '100%' : 0,
                        bgcolor: '#111',
                        transition: 'width 0.3s ease',
                      }}
                    />
                  </Box>
                </Box>
              );
            })}
          </Stack>

          {/* Footer Paragraph */}
          <Box
            width="100%"
            display="flex"
            justifyContent="space-between"
            alignItems="flex-end"
            px={1}
          >

            {/* Powered by Text */}
            <Box
              fontSize="0.9rem"
              color="#333"
              whiteSpace="nowrap"
              fontWeight={400}
              sx={{
                cursor: 'pointer',
              }}
              onClick={() => window.open('https://eigensol.com', '_blank')}
            >
              <small style={{color: "#333", fontWeight: 400, fontSize: '0.7rem'}}>
                powered by
              </small>
               <br/> <Box component="span" fontWeight="bold" display="inline" color='#111'>Eigen</Box>Sol
            </Box>

            {/* Info Tooltip */}
            <Tooltip
              title={
                <Box sx={{ padding: '10px'}}>
                  <Typography fontWeight="bold" color='#fff' sx={{ fontSize: '12px'}}>About This App</Typography>
                  <Typography variant="caption" color='#fff' sx={{ fontSize: '10px'}}>This platform lets you explore interactive AI agents across domains.</Typography>
                </Box>
              }
              arrow
              placement="top-start"
            >
              <HelpOutlineIcon sx={{ fontSize: 20, color: '#333', margin: '5px' }} />
            </Tooltip>

          </Box>

        </Stack>

        {/* Right Side (Slider or other content) */}
        <Box
          flex={1}
          py={5}
          pr={5}
          backgroundColor="#fff0"
        >
          <Swiper
            direction="vertical"
            slidesPerView={1}
            onSwiper={(swiper) => (swiperRef.current = swiper)}
            onSlideChange={(swiper) => setActiveIndex(swiper.activeIndex)}
            mousewheel
            style={{ height: '100%', paddingTop: '1vw', paddingBottom: '1vw', paddingRight: '1vw' }}
            effect="cards" // Add card effect
            modules={[EffectCards, Mousewheel]} // Include EffectCards module
          >
            {Object.entries(slides).map(([label, element], i) => (
              <SwiperSlide key={i}>
                <Box
                  height="100%"
                  width="100%"
                  // display="flex"
                  // alignItems="center"
                  // justifyContent="center"
                  // fontSize="32px"
                  // bgcolor="#fff"
                  // borderRadius="2vw"
                >
                  {element}
                </Box>
              </SwiperSlide>
            ))}
          </Swiper>
        </Box>
      </Box>
    </AppTheme>
  );
}

export default GuestLayout;